.qcard {

  border-radius: 2px;

  height: 200px;
  margin: 1rem;
  position: relative;
  width: 88%;

  background-color: rgb(233, 206, 171);
  /* background-color: rgb(139, 39, 86); */
  box-shadow: 5px 6px 6px rgb(49, 46, 46), 0 3px 3px rgb(59, 58, 58);
  text-align: left;
  padding: 20px;
  border-radius: 50px;
}

.submitAnswer {

  border-radius: 50px;
  background-color: transparent;
  border-style: solid;
  border-width: 1px;
  border-color: black;
}

